.company{
    text-align: center;
    position: sticky;
    top: 20%;
    left: 40%;
    transform: translate(50%);
    width: 200%;
   
}
.name{
    position: relative;
    top: -10px;
    right: 200px;
}
.input{
    width: 50%;
    padding: 5px;
    font-size: medium;
    position: relative;
    top: -20px;
    align-items: center;
    justify-content: center;
}
.button{
    padding: 7px;
    background-color: rgb(81, 144, 239);
    border-radius: 5px;
}
hr{
    width: 100%;
}/* account.css */

ul {
    list-style-type: none; 
    margin: 0;
}

li {
    display: table;
    width: 100%;
    border-bottom: 1px solid #ddd; 
    padding: 10px;
    box-sizing: border-box;
}

.list {
    display: table-row; 
    width: 100%;
}

.li {
    display: table-cell; 
    padding: 5px;
}

input[type="checkbox"] {
    margin-right: 10px; 
}

h4 {
    display: inline; 
    margin: 0;
}

.para {
    display: inline; 
    margin-left: 5px;
}

.delete {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 3px;
    margin-top: 5px;
}

.delete:hover {
    background-color: #e60000;
}

hr {
    width: 100%;
    border: 0;
    border-top: 1px solid #ddd;
}
.tab{
    cursor: pointer;
}