/* account.css */

.account {
    padding: 20px;
    position: relative;
    left: 150%;
}

form {
    margin-bottom: 20px;
}

.label {
    display: block;
    margin-bottom: 5px; 
    font-weight: bold;
    margin-left: -80px; 
}

input {
    padding: 5px;
    font-size: medium;
    font-weight: bold;
    width: 100%; 
    box-sizing: border-box; 
}

table {
    width: 100%;
    border-collapse: collapse;
}

table, th, td {
    border: 1px solid #ddd;
}

th, td {
    padding: 8px;
    text-align: left;
    position: relative;
    padding-right: 30px;
}

th {
    background-color: #f2f2f2;

}
