
* {
    box-sizing: border-box;
  }
  body {
    background: #1c8ef9 !important;
    min-height: 100vh;
    display: flex;
    font-weight: 400;
    font-family: "Fira Sans", sans-serif;
  }
.auth-wrapper{
    border: 1px solid black;
    transform: translate(-50%,-50%);
    position: absolute;
    top: 50%;
    left: 50%;
    height: 80%;
    padding: 10px;
    width: 25%;

}
.auth-inner{
    border: 1px solid rgb(218, 28, 28);
    background-color: white;
    height: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
}
h3{
font-size: 20px;

}
label{
width: 50%;
font-size: medium;
font-weight: bolder;
position: relative;
right: 100px;
top: 20px;

}

.form-control{
    width: 50%;
    padding: 5px;
    font-size: medium;
    position: relative;
    margin-top: 10px;

}
.btn{
    padding: 7px;
    font-size: medium;
    font-weight: bolder;
    position: relative;
    top: 20px;
    border-radius: 5px;
    width: 40%;
    background-color: rgb(61, 122, 236);
    
}
.forgot{
    align-items: left;
    font-weight: bolder;
    padding: 20px;
    position: relative;
    top: 10px;
    color: grey;
    text-decoration: none;
}


 