.auth {
  border: 1px solid black;
  transform: translate(-50%, -50%);
  position: fixed;
  top: 50%;
  left: 50%;
  height: 60%;
  padding: 10px;
  width: 25%;
}
.auth-in {
  border: 1px solid rgb(218, 28, 28);
  background-color: white;
  height: 100%;
  text-align: center;
}
.heading {
  font-size: 30px;
}
.label {
  font-size: medium;
  font-weight: bolder;
}

.form {
  width: 50%;
  padding: 5px;
  font-size: medium;
  position: relative;
  margin-top: 30px;
  align-items: center;
  justify-content: center;
}
.btn {
  padding: 7px;
  font-size: medium;
  font-weight: bolder;
  position: relative;
  top: 20px;
  border-radius: 5px;
  width: 40%;
  background-color: rgb(61, 122, 236);
}
.forgot {
  align-items: left;
  font-weight: bolder;
  padding: 20px;
  position: relative;
  top: 10px;
  color: rgb(90, 82, 82);
  text-decoration: none;
}
